export default {
  name: 'keyboard',
  data () {
    return {
      type: 0,
      chineseList: [
        '京', '津', '渝', '沪', '冀', '晋', '辽', '吉', '黑', '苏',
        '浙', '皖', '闽', '赣', '鲁', '豫', '鄂', '湘', '粤', '琼',
        '川', '贵', '云', '陕', '甘', '青', '蒙', '桂', '宁', '新',
        '藏', '使', '领', '警', '学', '港', '澳'
      ],
      letterList: [
        1, 2, 3, 4, 5, 6, 7, 8, 9, 0,
        'Q', 'W', 'E', 'R', 'T', 'Y', 'U', 'I', 'O', 'P',
        'A', 'S', 'D', 'F', 'G', 'H', 'J', 'K', 'L',
        'Z', 'X', 'C', 'V', 'B', 'N', 'M'
      ],
      currentIndex: 0,
      highlight: false
    }
  },
  props: {
    visible: false,
    electric: false
  },
  methods: {
    touchStart (index) {
      this.currentIndex = index
      this.highlight = true
    },

    touchEnd (index, type) {
      this.highlight = false

      let appended = ''

      if (type === 0) {
        appended = this.chineseList[this.currentIndex].toString()
      } else if (type === 1) {
        appended = this.letterList[this.currentIndex].toString()
      }

      this.$emit('licenceCompleted', appended)
    },
    finishPutting () {
      this.$emit('finishPutting')
    },
    switchType () {
      this.type = this.type === 0 ? 1 : 0
    },
    rollBack () {
      this.$emit('rollBack')
    }
  }
}



